.app-map-ovelay{
    height: 100vh;
    width: 100%;
}
.app-clock {
    color: #000000;
    font: 1em sans-serif;
    font-size: 56px;
    position: absolute;
    top: 3;
    z-index: 1;
    left: 35%;
    letter-spacing: 12px;
}

.app-date {
    padding-top: 54px;
    color: #000000;
    font: 1em sans-serif;
    font-size: 24px;
    position: absolute;
    top: 5;
    z-index: 1;
    left: 33%;
}
table {
    width: 210px;
    padding-top: 54px;
    border-collapse: collapse;
    position: absolute;
    top: 3%;
    left: 2%;
    background-color: #ffffff;
    z-index: 1;
    opacity: 0.8;
    box-shadow: 3px 3px grey;
}

table,
th,
td {
    border: 1px solid grey;
}


.app-crime-summary {
    background-color: lightgray;
    height: 248px;
    position: absolute;
    bottom: 2%;
    right: 13%;
    width: 672px;
    z-index: 1;
    opacity: 0.9;
    box-shadow: 3px 3px grey;
}

.app-crime-summary-heading {
    color: #ffffff;
}

.app-crime-summary-info{
    position: absolute;
    color: #ffffff;
    margin-left: 15px;
}
.app-crime-summary-info p {
    display: inline;
    margin-right: 3rem;
    margin-bottom: 0%;
    text-decoration: underline;
}
.app-form {
    background-color: lightgray;
    height: 442x;
    width: 146px;
    position: absolute;
    top: 1%;
    right: 1%;
    z-index: 1;
    padding: 12px;
    opacity: 0.8;
    box-shadow: 3px 3px grey;
}

.app-form-select-month {
    width: 100%;
}
.app-login-container {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.app-login {
    width: 369px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}

.app-login-form {
    background-color: #777777;
    padding: 2rem;
    padding-top: 7rem;
}

.app-login-btn{
    width: 100%;
    background-color: #37464d;
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 5px;
}

.app-login-body{
    background-color: #8c8d8c;
    width: 100%;
    height: 100vh;
}

.app-login-input{
    background-color: #cfcfcf;
}

.app-login-heading{
    color: #ffffff;
}

.app-login-logo{
    z-index: 1;
    width: 200px;
    position: absolute;
    left: 50%;
    top: 0%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); 
}
.app-add-form-card{
    padding: 12px;
    background-color: #000000;
    width: 680px;
    height: 240px;
    z-index: 1;
    position: absolute;
    opacity: .8;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    border-radius: 10px;
}

.app-add-form-heading{
    color: #ffffff;
}

.app-add-form-close-button{
    position: absolute;
    border-radius: 12px;
    width: 36px;
    right: 2%;
    top: 2%;
}

.app-add-form-label{
    color: #ffffff;
}

.app-form-select-month {
    width: 100%;
}
